
import { AppNavigator } from '../navigator/app-navigator'
import { AppMenu, MENUS } from './menus'
export default {
    name: 'app-menu',
    props: {
        currentPath: { type: String },
    },
    setup() {
        const navigator = AppNavigator.use.inject();

        return {
            menu: MENUS,
            handleClickMenu: (menu: AppMenu) => {
                navigator.methods.go(menu.page)
            },
        }
    },
}
